// pages/index.js
import News from './Components/News';

export default function Home() {
  const pageSize = 12;
  const apiKey = process.env.NEXT_PUBLIC_NEWS_API; // Note: NEXT_PUBLIC prefix

  return (
    <News 
      apiKey={apiKey} 
      pageSize={pageSize} 
      country='us' 
      category='general' 
    />
  );
}
